import React, { Component } from "react";

export default class Start extends Component {
	render() {
		return <div className='text-justify px-3 pt-2'>
			<h1 className='text-muted font-weight-bold'>SISTEMA OPERATIVO</h1>
			<p>
				WiFiSlax es una distribución GNU/Linux en formato *.iso con
				uncionalidades pensadas y diseñadas para la auditoría de seguridad
				y relacionada con la seguridad informática en general.
			</p>
			<p>
				Incluye una larga lista de herramientas de seguridad y auditoría
				listas para ser utilizadas, entre las que destacan numerosos escáner
				de puertos y vulnerabilidades, herramientas para creación y diseño
				de exploits, sniffers, herramientas de análisis forense y
				herramientas para la auditoría wireless, además de añadir una serie
				de útiles lanzadores.
			</p>
			<img className='image' src="images/image3.jpg" width="700px" alt='imagen' />
			<p>
				Posee una gran integración de varios controladores de red no
				oficiales en el kernel de Linux, y da así soporte inmediato para un
				gran número de tarjetas de red cableadas e inalámbricas.
			</p>
			<p>
				Wifislax es una de las suites de hacking ético más completas que
				podemos encontrar para comprobar la seguridad y hackear prácticamente
				cualquier red Wi-Fi. Esta suite de seguridad se basa en la
				distribución Linux Slackware y viene de base con una gran colección
				de herramientas de seguridad y auditoría, tanto para comprobar la
				seguridad de las redes Wi-Fi como de otros aspectos de una red, como
				los puertos y su protección contra diferentes exploits, entre otros.
			</p>
			<p>
				A pesar de que existen otras distribuciones de hacking ético en la
				red (como Kali Linux, BackBox o Parrot Security OS), Wifislax sigue
				teniendo a día de hoy un importante público, sobre todo entre los
				usuarios que hace tiempo utilizaron esta suite y se hicieron a ella,
				ya que, aunque puede no ser la más sencilla de utilizar, es de las
				más completas para auditar todo tipo de redes, en especial redes
				Wi-Fi.
			</p>
		</div>
	}
}